import React, { useEffect } from 'react';
import PropTypes from 'prop-types';
import { Row, Col } from 'reactstrap';
import { withRouter, Redirect } from 'react-router-dom';

import { historyData } from './components/History/History.content';

import styles from './Blog.module.scss';

const Navbar = React.lazy(() => import('../Landing/components/Navbar'));
const Footer = React.lazy(() => import('../Landing/components/Footer'));
const History = React.lazy(() => import('./components/History'));


const Blog = ({ match }) => {
  const page = historyData.find(c => c.url === match.params.blog_page);
  const Component = page && page.component;

  useEffect(() => {
    window.scrollTo(0, 0);
  }, []);

  if (!page) {
    return <Redirect to={{ pathname: `/blog/${historyData[0].url}` }} />;
  }

  return (
    <React.Suspense fallback={<div />}>
      <Navbar fixed />
      <div className={styles.blogContainer}>
        <div className={styles.blogContent}>
          <Row noGutters>
            <Col xs={12} md={9}>
              {Component && <Component />}
            </Col>
            <Col xs={12} md={3}>
              <History />
            </Col>
          </Row>
        </div>
      </div>
      <Footer />
    </React.Suspense>
  );
}

Blog.defaultProps = {
};

Blog.propTypes = {
  match: PropTypes.object.isRequired,
};

export default withRouter(Blog);
